body{margin:0;border:none;display:flex;flex-direction:column;align-items:center;margin-top:100px}.chart{font-size:10px;font-family:sans-serif;text-align:right;padding:3px;margin:1px;color:white;width:1283px;border-top:none;border-bottom:1px solid #ebebeb;border-right:none;border-left:none}button{display:inline-block;box-sizing:border-box;border:none;padding:10px 15px;margin:15px 10px;text-decoration:none;color:#ffffff;font-family:sans-serif;font-size:1rem;cursor:pointer;text-align:center;transition:background 250ms ease-in-out,  transform 150ms ease;-webkit-appearance:none;-moz-appearance:none}#shuffle-buttons button{background-color:#5e798e}#sort-buttons button{background:#ff6666}
